import React, { Component } from 'react';
import HeaderComponent from '../../components/pageHeader';
import './detail.css';
// import "highlight.js/styles/monokai-sublime.css";
// import Highlight from "react-highlight";
import { response } from '../../posts';
const ReactMarkdown = require('react-markdown');

export default class DetailPage extends Component {
	state = {
		infos: {},
	};
	componentDidMount() {
		const { data = { list: [], count: 0 } } = response;
		const { list } = data;
		this.setState({ infos: list[0] }, () => {
			let { infos } = this.state;
			document.title = infos.title;
			document.addEventListener('visibilitychange', () => {
				if (document.hidden) {
					document.title = 'ä¸»äººï¼Œå¿«ç‚¹å›æ¥å“¦~';
				} else {
					document.title = infos.title;
				}
			});
		});
	}
	renderContent() {
		let { infos } = this.state;
		return (
			<>
				<div id="main" className="content">
					<div className="container">
						<header className="entry-header">
							<h1 className="entry-title"># {infos.title}</h1>
						</header>
						<ReactMarkdown source={infos.mark_content} />
						{/* <Highlight className="javascript">Hello React</Highlight> */}
					</div>
				</div>
			</>
		);
	}
	onClick = () => {
		alert(1);
	};
	render() {
		return (
			<>
				<HeaderComponent />
				{this.renderContent()}
			</>
		);
	}
}
